import React from 'react';
import { useUser } from '../contexts/UserContext';
import { useNavigation } from '../contexts/NavigationContext';
import { useCard } from '../contexts/CardContext';
import Card from './ui/Card';
import Button from './ui/Button';
import { 
  CreditCard, 
  TrendingUp,
  DollarSign,
  ArrowUpRight,
  ArrowDownRight,
  Calendar,
  PieChart,
  Zap
} from 'lucide-react';
import './HomeScreen.css';

const HomeScreen = () => {
  const { user } = useUser();
  const { navigateTo } = useNavigation();
  const { cardData, getTotalSpent, getAvailableLimit, expenses } = useCard();

  const getGreeting = () => {
    const hour = new Date().getHours();
    if (hour < 12) return 'Bom dia';
    if (hour < 18) return 'Boa tarde';
    return 'Boa noite';
  };

  const formatCurrency = (value) => {
    return new Intl.NumberFormat('pt-BR', {
      style: 'currency',
      currency: 'BRL'
    }).format(value);
  };

  // Estatsticas dos gastos
  const totalSpent = getTotalSpent();
  const availableLimit = getAvailableLimit();
  const totalLimit = cardData?.limit || 0;
  const usagePercentage = totalLimit > 0 ? (totalSpent / totalLimit) * 100 : 0;

  // Gastos dos ltimos 7 dias
  const last7Days = expenses.filter(expense => {
    const expenseDate = new Date(expense.date);
    const today = new Date();
    const diffTime = Math.abs(today - expenseDate);
    const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
    return diffDays <= 7;
  });

  const weeklySpent = last7Days.reduce((total, expense) => total + expense.amount, 0);



  return (
    <div className="home-screen-pro">
      {/* Hero Section */}
      <div className="hero-section">
        <div className="hero-content">
          <div className="greeting-pro">
            <h1 className="hero-title">
              {getGreeting()}, <span className="gradient-text">{user.name || 'Usurio'}</span>
            </h1>
            <p className="hero-subtitle">Gerencie suas finanas com inteligncia</p>
          </div>
          <div className="hero-badge">
            <Zap size={16} />
            <span>Atualizado agora</span>
          </div>
        </div>
      </div>

      {/* Dashboard Grid */}
      <div className="dashboard-grid">
        {/* Card Principal - Resumo Financeiro */}
        <Card variant="gradient" className="financial-overview">
          <div className="overview-header">
            <div>
              <h3>Viso Geral</h3>
              <p className="overview-subtitle">{cardData?.name || 'Carto Principal'}</p>
            </div>
            <div className="overview-icon">
              <CreditCard size={24} />
            </div>
          </div>
          
          <div className="overview-amount">
            <span className="amount-label">Disponvel</span>
            <h2 className="amount-value">{formatCurrency(availableLimit)}</h2>
          </div>

          <div className="overview-progress">
            <div className="progress-bar">
              <div 
                className="progress-fill"
                style={{ width: `${usagePercentage}%` }}
              />
            </div>
            <div className="progress-labels">
              <span>Usado: {formatCurrency(totalSpent)}</span>
              <span>{usagePercentage.toFixed(0)}%</span>
            </div>
          </div>
        </Card>

        {/* Stats Cards */}
        <Card hover className="stat-card-pro">
          <div className="stat-icon-wrapper success">
            <ArrowUpRight size={20} />
          </div>
          <div className="stat-info">
            <span className="stat-label-pro">Limite Total</span>
            <h3 className="stat-value-pro">{formatCurrency(totalLimit)}</h3>
            <span className="stat-change positive">+100% disponvel</span>
          </div>
        </Card>

        <Card hover className="stat-card-pro">
          <div className="stat-icon-wrapper warning">
            <ArrowDownRight size={20} />
          </div>
          <div className="stat-info">
            <span className="stat-label-pro">Esta Semana</span>
            <h3 className="stat-value-pro">{formatCurrency(weeklySpent)}</h3>
            <span className="stat-change">{last7Days.length} transaes</span>
          </div>
        </Card>

        <Card hover className="stat-card-pro">
          <div className="stat-icon-wrapper info">
            <Calendar size={20} />
          </div>
          <div className="stat-info">
            <span className="stat-label-pro">Prxima Fatura</span>
            <h3 className="stat-value-pro">
              {cardData?.nextRecharge ? new Date(cardData.nextRecharge).toLocaleDateString('pt-BR', { day: '2-digit', month: 'short' }) : '--'}
            </h3>
            <span className="stat-change">Recarga automtica</span>
          </div>
        </Card>
      </div>

      {/* Grficos */}
      {expenses.length > 0 && (
        <Card className="charts-card-pro">
          <div className="charts-header">
            <div>
              <h3>Anlise de Gastos</h3>
              <p>Visualize seus padres de consumo</p>
            </div>
            <Button 
              variant="ghost" 
              size="sm"
              icon={<PieChart size={16} />}
              onClick={() => navigateTo('expenses')}
            >
              Ver Detalhes
            </Button>
          </div>
          <div className="charts-content">
            {/* Aqui voc pode adicionar grficos mais tarde */}
            <div className="chart-placeholder">
              <PieChart size={48} />
              <p>Grficos em desenvolvimento</p>
            </div>
          </div>
        </Card>
      )}

      {/* Quick Actions */}
      <div className="quick-actions-pro">
        <h3 className="section-title">Aes Rpidas</h3>
        <div className="actions-grid">
          <Button 
            variant="primary" 
            size="lg"
            fullWidth
            icon={<CreditCard size={20} />}
            onClick={() => navigateTo('cards')}
          >
            Meus Cartes
          </Button>
          <Button 
            variant="secondary" 
            size="lg"
            fullWidth
            icon={<TrendingUp size={20} />}
            onClick={() => navigateTo('expenses')}
          >
            Ver Gastos
          </Button>
        </div>
      </div>
    </div>
  );
};

export default HomeScreen;